import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject } from 'rxjs';
import { tap, mergeMap, map, catchError } from 'rxjs/operators'
import { InterviewQuestions } from './Interviewquestions';
import { INTERVIEW_QUESTIONS_DATA_URL } from '../constants';

@Injectable()
export class InterviewQuestionsService {

    constructor(private http: HttpClient) {
    }

    public getInterViewQuesionsData(courseId) {
        return this.http.get<InterviewQuestions>("https://codingkrishna.github.io/api/interview_questions.json").pipe(
            tap((data) => {
                console.log(data)
            }),
            catchError((err) => Observable.throw(err.json().error))
        )
    }

}
