
import { Component, OnInit } from '@angular/core';
import { CourseService } from '../../services/course/course.service';
import { Router, ActivatedRoute } from '@angular/router';
import * as aceEditor from '../../../editor.js';

@Component({
  selector: 'tutorial-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.css']
})
export class VideoComponent implements OnInit {

    topicId:String = "1";
    projectURL:String;
    private courseId: number;
    videoId:String;
    sampleCode:String;
    textTutorialURL: String;
    constructor( private route: ActivatedRoute,
    private router: Router, private courseServ: CourseService) { }

  ngOnInit() {
    this.route.parent.params.subscribe(params => {
      this.courseId = +params["courseId"];
      let topic = this.courseServ.getCourseDetails(this.courseId).topics[0];
      this.updateTopicDetails(topic);
    });
    this.route.params.subscribe( params => {
      let topic = this.courseServ.getTopicDetails(this.courseId, params.topicId);
      this.updateTopicDetails(topic);
      // this.sampleCode = topic.code;
      // console.log('courseId-->', this.courseId, 'topic id-->', params.topicId, 'topic->', topic)
      // this.projectURL = topic.projectUrl;
      // this.videoId = "https://www.youtube.com/embed/"+ topic.videoId;
    });
   
  }

  updateTopicDetails(topic){
    console.log('VideoComponent->updateTopicDetails courseId', this.courseId, ' topic-->', topic);
    if(topic.videoId !== ""){
      this.videoId = "https://www.youtube.com/embed/"+ topic.videoId;
    } else {
      this.videoId = '';
    }
    this.textTutorialURL = topic.textTutorialURL;
    this.projectURL = topic.projectUrl;
    this.sampleCode = topic.code;
  }

  ngAfterViewInit(){
    setTimeout(() => {
     aceEditor.reloadEditor();
    }, 1000);
  }

}
